import { Component, ElementRef, Injector, NgModule, OnDestroy, ViewEncapsulation } from '@angular/core';
import { Router, RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { Subscription } from 'rxjs';

import { OSharedModule } from '../../shared';
import { DialogService, LoginService, OUserInfoService, UserInfo } from '../../services';

export const DEFAULT_INPUTS_O_USER_INFO = [];

export const DEFAULT_OUTPUTS_O_USER_INFO = [];

@Component({
  moduleId: module.id,
  selector: 'o-user-info',
  inputs: DEFAULT_INPUTS_O_USER_INFO,
  outputs: DEFAULT_OUTPUTS_O_USER_INFO,
  template: `
    <div class="o-user-info-container" fxLayout="row" fxLayoutAlign="center">
      <ng-container *ngIf="existsUserInfo">
        <div class="o-user-info-wrapper" fxLayout="row" fxLayoutAlign="center center">
          <div fxLayout="row" fxLayoutAlign="center center" class="o-user-info-avatar-wrapper">
            <img [src]="avatar" *ngIf="avatar" />
          </div>
          <span class="o-user-info-username" *ngIf="username"> {{ username }}</span>
          <mat-icon [matMenuTriggerFor]="menu" svgIcon="ontimize:keyboard_arrow_down"></mat-icon>
        </div>
        <mat-menu #menu="matMenu" yPosition="below">
          <button type="button" mat-menu-item (click)="onSettingsClick()">
            <mat-icon svgIcon="ontimize:settings"></mat-icon>
            <span>{{ 'SETTINGS' | oTranslate }}</span>
          </button>
          <button type="button" mat-menu-item (click)="onLogoutClick()">
            <mat-icon svgIcon="ontimize:power_settings_new"></mat-icon>
            <span>{{ 'LOGOUT' | oTranslate }}</span>
          </button>
        </mat-menu>
      </ng-container>
    </div>
  `,
  styles: [`
    .o-user-info .o-user-info-container{cursor:pointer;height:100%}.o-user-info .o-user-info-avatar-wrapper{width:30px;height:30px;overflow:hidden;border-radius:100%}.o-user-info .o-user-info-avatar-wrapper img{max-width:100%;height:auto}.o-user-info .o-user-info-username{padding-left:6px}.o-user-info mat-icon{padding-left:4px;font-size:16px;line-height:26px}
  `],
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-user-info]': 'true'
  }
})
export class OUserInfoComponent implements OnDestroy {

  public static DEFAULT_INPUTS_O_USER_INFO = DEFAULT_INPUTS_O_USER_INFO;
  public static DEFAULT_OUTPUTS_O_USER_INFO = DEFAULT_OUTPUTS_O_USER_INFO;

  protected dialogService: DialogService;
  protected loginService: LoginService;
  protected oUserInfoService: OUserInfoService;

  userInfoSubscription: Subscription;
  protected userInfo: UserInfo;

  constructor(
    protected elRef: ElementRef,
    protected injector: Injector,
    protected router: Router
  ) {
    this.dialogService = this.injector.get(DialogService);
    this.loginService = this.injector.get(LoginService);
    this.oUserInfoService = this.injector.get(OUserInfoService);

    this.userInfo = this.oUserInfoService.getUserInfo();
    this.userInfoSubscription = this.oUserInfoService.getUserInfoObservable().subscribe(res => {
      this.userInfo = res;
    });
  }

  ngOnDestroy() {
    this.userInfoSubscription.unsubscribe();
  }

  onLogoutClick() {
    this.loginService.logoutWithConfirmationAndRedirect();
  }

  onSettingsClick() {
    this.router.navigate(['main/settings']);
  }

  get existsUserInfo(): boolean {
    return this.userInfo !== undefined;
  }

  get avatar(): string {
    return this.userInfo ? this.userInfo.avatar : undefined;
  }

  get username(): string {
    return this.userInfo ? this.userInfo.username : undefined;
  }

}

@NgModule({
  declarations: [OUserInfoComponent],
  imports: [CommonModule, OSharedModule, RouterModule],
  exports: [OUserInfoComponent]
})
export class OUserInfoModule { }
