import { AfterViewInit, ChangeDetectionStrategy, Component, Injector, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { MatCheckboxChange, MatDialogRef, MatListOption, MatSelectionList } from '@angular/material';

import { OTableBaseDialogClass } from '../o-table-base-dialog.class';

@Component({
  moduleId: module.id,
  selector: 'o-table-store-configuration-dialog',
  template: `
    <span mat-dialog-title>{{ 'TABLE.BUTTONS.SAVE_CONFIGURATION' | oTranslate }}</span>

    <mat-dialog-content>
      <div mat-subheader>{{ 'TABLE.DIALOG.SAVE_CONFIGURATION' | oTranslate }}</div>
      <form #form [formGroup]="formGroup" fxLayout="column">
        <mat-form-field>
          <input matInput [matTooltip]="tooltipText" [matTooltipClass]="tooltipClass" placeholder="{{ 'TABLE.DIALOG.CONFIGURATION_NAME' | oTranslate }}"
            formControlName="name" required>
          <mat-error *ngIf="formGroup.controls['name'].hasError('required')" text="{{ 'FORM_VALIDATION.REQUIRED' | oTranslate }}"></mat-error>
        </mat-form-field>
        <mat-form-field class="example-full-width">
          <textarea matInput placeholder="{{ 'TABLE.DIALOG.CONFIGURATION_DESCRIPTION' | oTranslate }}" formControlName="description" rows="4"
            cols="50"></textarea>
        </mat-form-field>
        <div class="inner-subheader" mat-subheader>{{ 'TABLE.DIALOG.CONFIGURATION_PROPERTIES' | oTranslate }}</div>
        <div fxLayout="column">
          <mat-checkbox (change)="onSelectAllChange($event)" [checked]="areAllSelected()" [indeterminate]="isIndeterminate()" class="select-all-checkbox">
            {{ 'SELECT_ALL' | oTranslate }}
          </mat-checkbox>

          <mat-selection-list #propertiesList dense class="o-table-save-configuration-dialog-list o-scroll">
            <mat-list-option checkboxPosition="before" *ngFor="let property of properties" [value]="property.property">
              <div fxLayout="row" fxLayoutAlign="center center">
                <span matLine fxFlex>{{ property.name | oTranslate }}</span>
                <mat-icon *ngIf="property.info" matTooltip="{{ property.info | oTranslate }}" class="o-tscd-list-tooltip">info</mat-icon>
              </div>
            </mat-list-option>
          </mat-selection-list>
        </div>
      </form>
    </mat-dialog-content>

    <mat-dialog-actions align="end">
      <button type="button" mat-stroked-button class="mat-primary" [mat-dialog-close]="false">{{ 'CANCEL' | oTranslate | uppercase }}</button>
      <button type="button" mat-stroked-button class="mat-primary" [mat-dialog-close]="true"
        [disabled]="!formGroup.valid || propertiesList.selectedOptions.selected.length===0">{{ 'SAVE' | oTranslate | uppercase }}</button>
    </mat-dialog-actions>
  `,
  styles: [`
    .select-all-checkbox{padding:6px 0}.o-table-save-configuration-dialog-list{max-height:120px;overflow-y:scroll}.o-table-save-configuration-dialog-list .mat-list-item{height:30px}.o-table-save-configuration-dialog-list .mat-list-item .o-tscd-list-tooltip.mat-icon{opacity:.25}
  `],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OTableStoreConfigurationDialogComponent extends OTableBaseDialogClass implements AfterViewInit {

  @ViewChild('propertiesList')
  public propertiesList: MatSelectionList;

  public properties: any[] = [{
    property: 'sort',
    name: 'TABLE.DIALOG.PROPERTIES.SORT',
    info: 'TABLE.DIALOG.PROPERTIES.SORT.INFO'
  }, {
    property: 'columns-display',
    name: 'TABLE.DIALOG.PROPERTIES.COLUMNS_DISPLAY',
    info: 'TABLE.DIALOG.PROPERTIES.COLUMNS_DISPLAY.INFO'
  }, {
    property: 'quick-filter',
    name: 'TABLE.DIALOG.PROPERTIES.QUICK_FILTER',
    info: 'TABLE.DIALOG.PROPERTIES.QUICK_FILTER.INFO'
  }, {
    property: 'columns-filter',
    name: 'TABLE.DIALOG.PROPERTIES.COLUMNS_FILTER',
    info: 'TABLE.DIALOG.PROPERTIES.COLUMNS_FILTER.INFO'
  }, {
    property: 'page',
    name: 'TABLE.DIALOG.PROPERTIES.PAGE',
    info: 'TABLE.DIALOG.PROPERTIES.PAGE.INFO'
  }];

  public formGroup: FormGroup = new FormGroup({
    name: new FormControl('', [
      Validators.required
    ]),
    description: new FormControl('')
  });

  constructor(
    public dialogRef: MatDialogRef<OTableStoreConfigurationDialogComponent>,
    protected injector: Injector
  ) {
    super(injector);
    this.setFormControl(this.formGroup.get('name'));
  }

  public ngAfterViewInit(): void {
    this.propertiesList.selectAll();
  }

  public areAllSelected(): boolean {
    return this.propertiesList && this.propertiesList.options && this.propertiesList.options.length === this.propertiesList.selectedOptions.selected.length;
  }

  public onSelectAllChange(event: MatCheckboxChange): void {
    event.checked ? this.propertiesList.selectAll() : this.propertiesList.deselectAll();
  }

  public getConfigurationAttributes(): any {
    return this.formGroup.value;
  }

  public getSelectedTableProperties(): any[] {
    const selected: MatListOption[] = this.propertiesList.selectedOptions.selected;
    return selected.length ? selected.map(item => item.value) : [];
  }

  public isIndeterminate(): boolean {
    return !this.areAllSelected();
  }

}
