import { Component, Inject, ViewEncapsulation, ChangeDetectionStrategy, ChangeDetectorRef, Injector } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Util, Codes } from '../../../../../utils';
import { OColumn } from '../../../o-table.component';
import { DragDropService } from '@churchs19/ng2-dnd';

@Component({
  moduleId: module.id,
  selector: 'o-table-visible-columns-dialog',
  template: `
    <span mat-dialog-title>{{ 'TABLE.BUTTONS.COLVIS' | oTranslate }}</span>

    <mat-dialog-content>
      <mat-list dnd-sortable-container [sortableData]="columns" [ngClass]="rowHeight">
        <ng-container *ngFor="let column of columns; let x = index">
          <mat-list-item *ngIf="column.showInList" (click)="onClickColumn(column)" dnd-sortable [sortableIndex]="x"
            [dragData]="column" [dragEnabled]="true" (onDragSuccess)="onDragSuccess($event)">
            <mat-icon mat-list-icon svgIcon="ontimize:drag_handle"></mat-icon>
            <span mat-line>{{ (column.title || column.attr) | oTranslate }}</span>
            <mat-icon *ngIf="column.visible" svgIcon="ontimize:visibility"></mat-icon>
            <mat-icon *ngIf="!column.visible" svgIcon="ontimize:visibility_off"></mat-icon>
          </mat-list-item>
        </ng-container>
      </mat-list>
    </mat-dialog-content>

    <mat-dialog-actions fxLayoutAlign="end center">
      <button type="button" mat-stroked-button [mat-dialog-close]="false">{{ 'CANCEL' | oTranslate | uppercase }}</button>
      <button type="button" mat-stroked-button [mat-dialog-close]="true">{{ 'ACCEPT' | oTranslate | uppercase }}</button>
    </mat-dialog-actions>
  `,
  styles: [`
    .o-table-visible-columns-dialog .mat-dialog-content.mat-dialog-content{overflow:auto;padding-top:0;margin-top:24px}.o-table-visible-columns-dialog .mat-dialog-content.mat-dialog-content .mat-list{padding-top:24px}.o-table-visible-columns-dialog .mat-dialog-content.mat-dialog-content .mat-list .mat-list-item{cursor:pointer}.o-table-visible-columns-dialog .mat-dialog-content.mat-dialog-content .mat-list .mat-list-item .mat-list-item-content{padding:0}.o-table-visible-columns-dialog .mat-dialog-content.mat-dialog-content .mat-list .mat-list-item .mat-list-item-content div.mat-list-text{padding:0 8px}.o-table-visible-columns-dialog .mat-dialog-content.mat-dialog-content .mat-icon[svgicon="ontimize:drag_handle"]{cursor:move}.o-table-visible-columns-dialog .mat-dialog-content.mat-dialog-content .mat-icon[svgicon="ontimize:visibility"],.o-table-visible-columns-dialog .mat-dialog-content.mat-dialog-content .mat-icon[svgicon="ontimize:visibility_off"]{width:32px}
  `],
  encapsulation: ViewEncapsulation.None,
  providers: [DragDropService],
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[class.o-table-visible-columns-dialog]': 'true'
  }
})
export class OTableVisibleColumnsDialogComponent {

  columns: Array<any> = [];
  protected cd: ChangeDetectorRef;
  rowHeight: string = Codes.DEFAULT_ROW_HEIGHT;

  constructor(
    protected injector: Injector,
    public dialogRef: MatDialogRef<OTableVisibleColumnsDialogComponent>,
    @Inject(MAT_DIALOG_DATA) data: any
  ) {
    try {
      this.cd = this.injector.get(ChangeDetectorRef);
    } catch (e) {
      // no parent form
    }
    if (Util.isArray(data.columnsData) && Util.isArray(data.originalVisibleColumns)) {
      let originalCols = data.originalVisibleColumns;
      data.columnsData.forEach((oCol: OColumn) => {
        this.columns.push({
          attr: oCol.attr,
          title: oCol.title,
          visible: oCol.visible,
          showInList: (oCol.definition !== undefined || oCol.visible || originalCols.indexOf(oCol.attr) !== -1)
        });
      });
    }
    if (Util.isDefined(data.rowHeight)) {
      this.rowHeight = data.rowHeight;
    }
  }

  getVisibleColumns(): Array<string> {
    return this.columns.filter(col => col.visible).map(col => col.attr);
  }

  getColumnsOrder(): Array<string> {
    return this.columns.map(col => col.attr);
  }

  onClickColumn(col: OColumn): void {
    col.visible = !col.visible;
  }

  onDragSuccess(arg: any) {
    this.cd.detectChanges();
  }
}
