import { Component, EventEmitter, Inject, Injector, OnInit, ViewChild, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef, MatListOption, MatSelectionList } from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';

import { ITableFiltersStatus } from '../../o-table-storage.class';
import { DialogService } from '../../../../../services/dialog.service';

@Component({
  moduleId: module.id,
  selector: 'o-table-load-filter-dialog',
  template: `
    <span mat-dialog-title>{{ 'TABLE.BUTTONS.FILTER_LOAD' | oTranslate }}</span>
    <mat-dialog-content fxLayout="column">
      <div mat-subheader>{{ 'TABLE.DIALOG.LOAD_FILTER' | oTranslate }}</div>
      <mat-selection-list #filterList dense class="o-table-load-filter-dialog-list">
        <mat-list-option *ngFor="let filter of filters " [value]="filter.name" checkboxPosition="before">
          <span matLine class="o-table-load-filter-dialog-list-title">{{ filter.name }}</span>
          <span matLine>{{ filter.description }}</span>
        </mat-list-option>
        <mat-list-item *ngIf="filters.length === 0">
          <span class="empty-filter-list">{{ 'TABLE.DIALOG.EMPTY_FILTER_LIST' | oTranslate }}</span>
        </mat-list-item>
      </mat-selection-list>
    </mat-dialog-content>

    <mat-dialog-actions align="end">
      <button type="button" mat-stroked-button [disabled]="filterList.selectedOptions.selected.length!==1"
        (click)="removeFilter(filterList.selectedOptions.selected[0].value)">{{ 'DELETE' | oTranslate | uppercase }}</button>
      <span fxFlex></span>
      <button type="button" mat-stroked-button class="mat-primary" [mat-dialog-close]="false">{{ 'CANCEL' | oTranslate | uppercase }}</button>
      <button type="button" mat-stroked-button class="mat-primary" [mat-dialog-close]="true"
        [disabled]="filterList.selectedOptions.selected.length!==1">{{ 'TABLE.BUTTONS.APPLY' | oTranslate | uppercase }}</button>
    </mat-dialog-actions>
  `,
  styles: [`

  `],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OTableLoadFilterDialogComponent implements OnInit {

  @ViewChild(MatSelectionList) filterList: MatSelectionList;

  filters: Array<ITableFiltersStatus> = [];

  onDelete: EventEmitter<string> = new EventEmitter();

  protected dialogService: DialogService;
  protected cd: ChangeDetectorRef;

  constructor(
    public dialogRef: MatDialogRef<OTableLoadFilterDialogComponent>,
    @Inject(MAT_DIALOG_DATA) data: Array<ITableFiltersStatus>,
    protected injector: Injector
  ) {
    this.loadFilters(data);
    this.dialogService = this.injector.get(DialogService);
    try {
      this.cd = this.injector.get(ChangeDetectorRef);
    } catch (e) {
      // no parent form
    }
  }

  ngOnInit(): void {
    this.filterList.selectedOptions = new SelectionModel<MatListOption>(false);
  }

  loadFilters(filters: Array<ITableFiltersStatus>): void {
    this.filters = filters;
  }

  getSelectedFilterName(): string {
    let selected: MatListOption[] = this.filterList.selectedOptions.selected;
    return selected.length ? selected[0].value : void 0;
  }

  removeFilter(filterName: string): void {
    this.dialogService.confirm('CONFIRM', 'TABLE.DIALOG.CONFIRM_REMOVE_FILTER').then(result => {
      if (result) {
        this.onDelete.emit(filterName);
        this.cd.detectChanges();
      }
    });
  }

}
