import {
  Component, Inject, Injector, forwardRef, ElementRef, OnInit,
  Optional,
  NgModule,
  ViewEncapsulation
} from '@angular/core';
import { CommonModule } from '@angular/common';
import { ValidatorFn } from '@angular/forms';
import { OSharedModule } from '../../../shared';
import { OValidators } from '../../../validators/o-validators';
import { OFormComponent } from '../../form/o-form.component';
import {
  OTextInputModule,
  OTextInputComponent,
  DEFAULT_INPUTS_O_TEXT_INPUT,
  DEFAULT_OUTPUTS_O_TEXT_INPUT
} from '../text-input/o-text-input.component';

export const DEFAULT_INPUTS_O_NIF_INPUT = [
  ...DEFAULT_INPUTS_O_TEXT_INPUT
];

export const DEFAULT_OUTPUTS_O_NIF_INPUT = [
  ...DEFAULT_OUTPUTS_O_TEXT_INPUT
];

@Component({
  moduleId: module.id,
  selector: 'o-nif-input',
  template: `
    <div fxLayout="row" fxLayoutAlign="space-between center" [formGroup]="getFormGroup()" [matTooltip]="tooltip"
      [matTooltipClass]="tooltipClass" [matTooltipPosition]="tooltipPosition" [matTooltipShowDelay]="tooltipShowDelay"
      [matTooltipHideDelay]="tooltipHideDelay">
      <mat-form-field [appearance]="appearance" [floatLabel]="floatLabel" [hideRequiredMarker]="hideRequiredMarker"
        [class.custom-width]="hasCustomWidth" class="icon-field" fxFlexFill>
        <mat-label *ngIf="labelVisible">{{ olabel | oTranslate }}</mat-label>
        <input matInput type="text" [id]="getAttribute()" [formControlName]="getAttribute()" [placeholder]="placeHolder"
          (focus)="innerOnFocus($event)" (blur)="innerOnBlur($event)" (change)="onChangeEvent($event)"
          [readonly]="isReadOnly" [required]="isRequired">
        <button type="button" *ngIf="showClearButton" matSuffix mat-icon-button (click)="onClickClearValue($event)">
          <mat-icon svgIcon="ontimize:close"></mat-icon>
        </button>
        <mat-icon matSuffix [class.mat-disabled]="!enabled" svgIcon="ontimize:perm_identity"></mat-icon>
        <mat-error *ngIf="hasError('required')" text="{{ 'FORM_VALIDATION.REQUIRED' | oTranslate }}"></mat-error>
        <mat-error *ngIf="hasError('invalidNIF')" text="{{ 'FORM_VALIDATION.NIF_FORMAT' | oTranslate}}"></mat-error>
        <mat-error *ngIf="hasError('invalidNIFLetter')" text="{{ 'FORM_VALIDATION.DNI_LETTER' | oTranslate}}"></mat-error>
        <mat-error *ngFor="let oError of getActiveOErrors()" text="{{ oError.text | oTranslate }}"></mat-error>
      </mat-form-field>
    </div>
  `,
  styles: [`

  `],
  inputs: DEFAULT_INPUTS_O_NIF_INPUT,
  outputs: DEFAULT_OUTPUTS_O_NIF_INPUT,
  encapsulation: ViewEncapsulation.None
})
export class ONIFInputComponent extends OTextInputComponent implements OnInit {

  public static DEFAULT_INPUTS_O_NIF_INPUT = DEFAULT_INPUTS_O_NIF_INPUT;
  public static DEFAULT_OUTPUTS_O_NIF_INPUT = DEFAULT_OUTPUTS_O_NIF_INPUT;

  constructor(
    @Optional() @Inject(forwardRef(() => OFormComponent)) form: OFormComponent,
    elRef: ElementRef,
    injector: Injector) {
    super(form, elRef, injector);
  }

  resolveValidators(): ValidatorFn[] {
    let validators: ValidatorFn[] = super.resolveValidators();
    //Inject NIF validator
    validators.push(OValidators.nifValidator);
    return validators;
  }

}

@NgModule({
  declarations: [ONIFInputComponent],
  imports: [OSharedModule, CommonModule, OTextInputModule],
  exports: [ONIFInputComponent, OTextInputModule]
})
export class ONIFInputModule {
}
