import { AfterViewInit, ChangeDetectionStrategy, ChangeDetectorRef, Component, ComponentFactory, ComponentFactoryResolver, ContentChildren, ElementRef, Injector, NgModule, OnDestroy, QueryList, ViewChild, ViewContainerRef, ViewEncapsulation } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs';

import { OSharedModule } from '../../shared';
import { InputConverter } from '../../decorators';
import { OTranslateService } from '../../services';

export const DEFAULT_INPUTS_O_MENU_CARD = [
  'title',
  'image',
  'icon',
  'tooltip',
  'buttonText : button-text',
  'disabledButton : disabled-button',
  'mainContainerLayout : main-container-layout',
  'secondaryContainerLayout : secondary-container-layout',
  'route',
  'detailComponent : detail-component',
  'detailComponentInputs : detail-component-inputs',
  'action'
];

export const DEFAULT_OUTPUTS_O_MENU_CARD = [];

@Component({
  moduleId: module.id,
  selector: 'o-card-menu-item',
  template: `
    <div class="o-card-menu-item-content" (click)="onClick()" fxLayout="column" fxFill fxLayoutAlign="space-between center"
      layout-padding>

      <div class="o-card-menu-item-main" fxFlex="{{ showSecondaryContainer ? '40' : '80' }}" [fxLayout]="mainContainerLayout"
        fxLayoutAlign="space-between center">
        <div *ngIf="useImage() || useIcon()" fxFlex="80" fxLayout="column" fxLayoutAlign="center center" class="o-card-menu-item-image-container">
          <img *ngIf="useImage()" [src]="image" />
          <mat-icon *ngIf="useIcon()" class="o-card-menu-item-icon" fxLayoutAlign="center center">{{ icon }}</mat-icon>
        </div>

        <div class="o-card-menu-item-title" fxFlex="20" fxLayout="column" fxLayoutAlign="center center" *ngIf="title !== undefined">{{
          title | oTranslate }}</div>

        <mat-icon class="o-card-menu-item-info" matTooltip="{{ tooltip | oTranslate }}" *ngIf="tooltip" svgIcon="ontimize:info_outline"></mat-icon>

        <ng-content select=".main-container"></ng-content>
      </div>

      <button type="button" *ngIf="buttonText !== undefined" [disabled]="disabledButton" (click)="onButtonClick()"
        mat-button>{{ buttonText | oTranslate }}</button>

      <div *ngIf="showSecondaryContainer" class="o-card-menu-item-secondary" fxFlex="50" [fxLayout]="secondaryContainerLayout"
        fxLayoutAlign="space-around center">
        <ng-content select=".secondary-container"></ng-content>
        <ng-container #menuCardContent *ngIf="detailComponent">
        </ng-container>
      </div>
    </div>
  `,
  styles: [`
    .o-card-menu-item{border-radius:3px}.o-card-menu-item .o-card-menu-item-main,.o-card-menu-item .o-card-menu-item-secondary{width:100%;padding:12px 0}.o-card-menu-item .o-card-menu-item-image-container{width:100%}.o-card-menu-item .o-card-menu-item-image-container .o-card-menu-item-icon{width:100%;height:100%;font-size:5em}.o-card-menu-item .o-card-menu-item-image-container img{max-width:100%;max-height:100%}.o-card-menu-item .mat-button,.o-card-menu-item .o-card-menu-item-title{font-weight:600}.o-card-menu-item .mat-button{width:100%;border-radius:5px;text-decoration:none}.o-card-menu-item .o-card-menu-item-main{position:relative}.o-card-menu-item .o-card-menu-item-icon,.o-card-menu-item .o-card-menu-item-title{cursor:default}.o-card-menu-item .o-card-menu-item-info{cursor:default;position:absolute;top:0;right:0}
  `],
  inputs: DEFAULT_INPUTS_O_MENU_CARD,
  outputs: DEFAULT_OUTPUTS_O_MENU_CARD,
  encapsulation: ViewEncapsulation.None,
  host: {
    '[class.o-card-menu-item]': 'true',
    '[class.mat-elevation-z1]': 'true',
    '[class.compact]': '!showSecondaryContainer'
  },
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class OCardMenuItemComponent implements AfterViewInit, OnDestroy {

  public static DEFAULT_INPUTS_O_MENU_CARD = DEFAULT_INPUTS_O_MENU_CARD;
  public static DEFAULT_OUTPUTS_O_MENU_CARD = DEFAULT_OUTPUTS_O_MENU_CARD;

  title: string;
  image: string;
  icon: string;
  tooltip: string;
  buttonText: string;
  @InputConverter()
  disabledButton: boolean = false;
  mainContainerLayout = 'column';
  secondaryContainerLayout = 'column';
  route: string;
  action: () => void;
  detailComponent: any;
  detailComponentInputs: Object;

  protected _detailComponentContainer: ViewContainerRef;

  @ViewChild('menuCardContent', { read: ViewContainerRef })
  set detailComponentContainer(content: ViewContainerRef) {
    this._detailComponentContainer = content;
  }

  get detailComponentContainer(): ViewContainerRef {
    return this._detailComponentContainer;
  }

  protected translateService: OTranslateService;
  protected translateServiceSubscription: Subscription;

  @ContentChildren('.secondary-container')
  secondaryContent: QueryList<any>;

  protected _showSecondaryContainer: boolean = true;

  constructor(
    protected injector: Injector,
    protected router: Router,
    protected actRoute: ActivatedRoute,
    protected resolver: ComponentFactoryResolver,
    protected cd: ChangeDetectorRef,
    protected elRef: ElementRef
  ) {
    this.translateService = this.injector.get(OTranslateService);
    this.translateServiceSubscription = this.translateService.onLanguageChanged.subscribe(() => {
      this.cd.detectChanges();
    });
  }

  ngAfterViewInit() {
    if (this.detailComponentContainer && this.detailComponent) {
      const factory: ComponentFactory<any> = this.resolver.resolveComponentFactory(this.detailComponent);
      const ref = this.detailComponentContainer.createComponent(factory);
      if (this.detailComponentInputs && ref.instance) {
        const keys = Object.keys(this.detailComponentInputs);
        for (let i = 0, len = keys.length; i < len; i++) {
          ref.instance[keys[i]] = this.detailComponentInputs[keys[i]];
        }
      }
    }
    this.showSecondaryContainer = (this.detailComponentContainer && this.detailComponent) || this.secondaryContent.length > 0;
    this.cd.detectChanges();
  }

  ngOnDestroy() {
    if (this.translateServiceSubscription) {
      this.translateServiceSubscription.unsubscribe();
    }
  }

  useImage(): boolean {
    return this.image !== undefined;
  }

  useIcon(): boolean {
    return this.icon !== undefined && this.image === undefined;
  }

  onButtonClick() {
    if (this.route) {
      this.router.navigate([this.route], {
        relativeTo: this.actRoute
      });
    } else if (this.action) {
      this.action();
    }
  }

  onClick() {
    if (this.buttonText === undefined) {
      this.onButtonClick();
    }
  }

  get showSecondaryContainer(): boolean {
    return this._showSecondaryContainer;
  }

  set showSecondaryContainer(val: boolean) {
    this._showSecondaryContainer = val;
    if (val) {
      this.elRef.nativeElement.classList.remove('compact');
    } else {
      this.elRef.nativeElement.classList.add('compact');
    }
  }

}

@NgModule({
  declarations: [OCardMenuItemComponent],
  imports: [CommonModule, OSharedModule],
  exports: [OCardMenuItemComponent]
})
export class OCardMenuItemModule { }
