import { Component, Injector, Input, ViewChild } from '@angular/core';
import { MatMenu } from '@angular/material/menu';

import { OComponentMenuItems } from '../../o-content-menu.class';
import { OContextMenuService } from '../../o-context-menu.service';

export const DEFAULT_CONTEXT_MENU_CONTENT_ITEM_INPUTS = [
  'items',
  'class'
];

@Component({
  moduleId: module.id,
  selector: 'o-wrapper-content-menu',
  template: `
    <mat-menu #childMenu="matMenu" [class]="class" [overlapTrigger]="false" [hasBackdrop]="false">
      <ng-container *ngFor="let child of items">
        <!-- Handle branch node menu items -->
        <span *ngIf="isGroup(child) && child.isVisible">
          <button mat-menu-item color="primary" [matMenuTriggerFor]="menu.childMenu" [disabled]="child.disabled">
            <mat-icon *ngIf="child.svgIcon !== undefined" [svgIcon]="child.svgIcon"></mat-icon>
            <mat-icon *ngIf="child.svgIcon === undefined">{{ child.icon }}</mat-icon>
            <span>{{ child.label | oTranslate}}</span>
          </button>
          <o-wrapper-content-menu #menu [items]="child.children" [class]="class"></o-wrapper-content-menu>
        </span>
        <!-- Handle leaf node menu items -->
        <button mat-menu-item (click)="onClick(child, $event)" [disabled]="child.disabled" *ngIf="isItem(child) && child.isVisible">
          <mat-icon *ngIf="child.svgIcon !== undefined" [svgIcon]="child.svgIcon"></mat-icon>
          <mat-icon *ngIf="child.svgIcon === undefined">{{ child.icon }}</mat-icon>
          <span>{{ child.label | oTranslate}} </span>
        </button>
        <!-- separator leaf node menu items -->
        <mat-divider *ngIf="isSepararor(child) && child.isVisible"></mat-divider>
      </ng-container>
    </mat-menu>
  `,
  styles: [`
    mat-divider.mat-divider:first-child,mat-divider.mat-divider:last-child{display:none}.mat-icon{display:inline-flex}
  `],
  inputs: DEFAULT_CONTEXT_MENU_CONTENT_ITEM_INPUTS
})
export class OWrapperContentMenuComponent {

  public class: string;
  @Input()
  public items: any[];
  @ViewChild('childMenu')
  public childMenu: MatMenu;
  @ViewChild(OWrapperContentMenuComponent)
  public menu: OWrapperContentMenuComponent;

  constructor(
    protected injector: Injector,
    protected menuService: OContextMenuService
  ) { }

  public onClick(item, event?): void {
    item.triggerExecute(item.data, event);
  }

  public isGroup(item): boolean {
    let isGroup = false;
    if (item && item.children && item.children.length > 0) {
      isGroup = true;
    }
    return isGroup;
  }

  public isSepararor(item): boolean {
    let isSepararor = false;
    if (item && item.type && item.type === OComponentMenuItems.TYPE_SEPARATOR_MENU) {
      isSepararor = true;
    }
    return isSepararor;
  }

  public isItem(item): boolean {
    let isItem = false;
    if (item && item.type && item.type === OComponentMenuItems.TYPE_ITEM_MENU) {
      isItem = true;
    }
    return isItem;
  }

}
