import { SelectionModel } from '@angular/cdk/collections';
import { Component, EventEmitter, Inject, Injector, OnInit, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef, MatListOption, MatSelectionList } from '@angular/material';

import { DialogService } from '../../../../../services';
import { ITableConfiguration } from '../../o-table-storage.class';

@Component({
  moduleId: module.id,
  selector: 'o-table-apply-configuration-dialog',
  template: `
    <span mat-dialog-title>{{ 'TABLE.BUTTONS.APPLY_CONFIGURATION' | oTranslate }}</span>

    <mat-dialog-content fxLayout="column">
      <div mat-subheader>{{ 'TABLE.DIALOG.APPLY_CONFIGURATION' | oTranslate }}</div>
      <mat-selection-list #configurationList dense class="o-table-apply-configuration-dialog-list">
        <mat-list-option checkboxPosition="before" [value]="default_configuration">
          <span matLine class="o-table-apply-configuration-dialog-list-title">{{ 'TABLE.DIALOG.APPLY_CONFIGURATION_DEFAULT' | oTranslate }}</span>
          <span matLine>{{ 'TABLE.DIALOG.APPLY_CONFIGURATION_DEFAULT_DESCRIPTION' | oTranslate }}</span>
        </mat-list-option>
        <mat-list-option checkboxPosition="before" *ngFor="let configuration of configurations; let i = index" [value]="configuration.name">
          <span matLine>{{ configuration.name }}</span>
          <span matLine>{{ configuration.description }}</span>
        </mat-list-option>
      </mat-selection-list>
    </mat-dialog-content>

    <mat-dialog-actions align="end">
      <button type="button" mat-stroked-button [disabled]="configurationList.selectedOptions.selected.length!==1 || isDefaultConfigurationSelected()"
        (click)="removeConfiguration(configurationList.selectedOptions.selected[0].value)">
        {{ 'DELETE' | oTranslate | uppercase }}
      </button>
      <span fxFlex></span>
      <button type="button" mat-stroked-button class="mat-primary" [mat-dialog-close]="false">{{ 'CANCEL' | oTranslate | uppercase }}</button>
      <button type="button" mat-stroked-button class="mat-primary" [mat-dialog-close]="true"
        [disabled]="configurationList.selectedOptions.selected.length!==1">{{ 'OK' | oTranslate | uppercase }}</button>
    </mat-dialog-actions>
  `,
  styles: [`

  `]
})
export class OTableApplyConfigurationDialogComponent implements OnInit {

  public default_configuration = 'OTableApplyConfigurationDialogComponent-default';
  public configurations: ITableConfiguration[] = [];

  public onDelete: EventEmitter<string> = new EventEmitter();

  @ViewChild(MatSelectionList)
  protected configurationList: MatSelectionList;

  protected dialogService: DialogService;

  constructor(
    public dialogRef: MatDialogRef<OTableApplyConfigurationDialogComponent>,
    @Inject(MAT_DIALOG_DATA) data: ITableConfiguration[],
    protected injector: Injector
  ) {
    this.loadConfigurations(data);
    this.dialogService = this.injector.get(DialogService);
  }

  public ngOnInit(): void {
    this.configurationList.selectedOptions = new SelectionModel<MatListOption>(false);
  }

  public loadConfigurations(configurations: ITableConfiguration[]): void {
    this.configurations = configurations;
  }

  public removeConfiguration(configurationName: string): void {
    this.dialogService.confirm('CONFIRM', 'TABLE.DIALOG.CONFIRM_REMOVE_CONFIGURATION').then(result => {
      if (result) {
        this.onDelete.emit(configurationName);
      }
    });
  }

  public isDefaultConfigurationSelected(): boolean {
    const selected: MatListOption[] = this.configurationList.selectedOptions.selected;
    const selectedValue = selected.length ? selected[0].value : void 0;
    return selectedValue === this.default_configuration;
  }

  public getSelectedConfigurationName(): string {
    const selected: MatListOption[] = this.configurationList.selectedOptions.selected;
    return selected.length ? selected[0].value : void 0;
  }

}
