import { AfterViewInit, ChangeDetectionStrategy, Component, ElementRef, Inject, ViewChild, ViewEncapsulation } from '@angular/core';
import { BehaviorSubject, Observable, fromEvent } from 'rxjs';
import { ColumnValueFilterOperator, IColumnValueFilter } from '../../header/o-table-header-components';
import { MAT_DIALOG_DATA, MatCheckboxChange, MatDialogRef, MatSelectionList, MatSlideToggleChange } from '@angular/material';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';

import { FormControl } from '@angular/forms';
import { OColumn } from '../../../o-table.component';
import { Util } from '../../../../../util/util';

export interface ITableFilterByColumnDataInterface {
  value: any;
  selected: boolean;
}

@Component({
  moduleId: module.id,
  selector: 'o-table-filter-by-column-data-dialog',
  template: `
    <span mat-dialog-title>{{ 'TABLE.BUTTONS.FILTER_BY_COLUMN' | oTranslate }}: {{ column.title | oTranslate }}</span>
    <div mat-dialog-content fxLayout="row" fxLayoutAlign="space-between stretch" class="dialog-list-container">
      <div fxLayout="column" class="content-wrapper"  [class.content-wrapper-fixed-dimension]="getFixedDimensionClass()">
        <mat-slide-toggle #customFilterSlide (change)="onSlideChange($event)" [checked]="(isCustomFilter | async)"
          *ngIf="isDefaultFilter | async">
          {{ 'TABLE.FILTER_BY_COLUMN.CUSTOM_FILTER' | oTranslate }}
        </mat-slide-toggle>
        <div fxFlex *ngIf="!(isCustomFilter | async);else customFilterTemplate" fxLayout="column">
          <mat-form-field class="hinted">
            <mat-icon matPrefix svgIcon="ontimize:search"></mat-icon>
            <input matInput #filter placeholder="{{ 'TABLE.FILTER' | oTranslate }}">
            <mat-hint>{{ 'TABLE.FILTER_BY_COLUMN.HINT_STAR' | oTranslate }}</mat-hint>
          </mat-form-field>
          <mat-checkbox (change)="onSelectAllChange($event)" [checked]="areAllSelected()"
            [indeterminate]="isIndeterminate()" [disabled]="!listData" class="select-all-checkbox">
            {{ 'TABLE.FILTER_BY_COLUMN.CHECK_ALL' | oTranslate }}
          </mat-checkbox>
          <span *ngIf="!listData" class="column-filter-empty-list">
            {{ 'TABLE.FILTER_BY_COLUMN.LIST_EMPTY_FILTER' | oTranslate }}
          </span>
          <span *ngIf="listData?.length === 0" class="column-filter-empty-list">{{ 'TABLE.FILTER_BY_COLUMN.LIST_EMPTY' |
            oTranslate }}</span>
          <mat-selection-list *ngIf="preloadValues || listData" #filterValueList fxFlex fxLayout="column"
            class="select-values-list">
            <mat-list-option *ngFor="let record of listData; let i = index" checkboxPosition="before"
              [selected]="record.selected" [value]="record.value">
              <ng-container *ngIf="!column.renderer">
                {{ record.value || ('TABLE.FILTER_BY_COLUMN.EMPTY_VALUE' | oTranslate) }}
              </ng-container>
              <ng-template *ngIf="column.renderer" [ngTemplateOutlet]="column.renderer.templateref"
                [ngTemplateOutletContext]="{ cellvalue: record.value, rowvalue: getRowValue(i) }"></ng-template>
            </mat-list-option>
          </mat-selection-list>
        </div>

      </div>
    </div>

    <mat-dialog-actions align="end">
      <button type="button" mat-stroked-button class="mat-primary" [mat-dialog-close]="false">
        {{ 'CANCEL' | oTranslate | uppercase }} </button>
      <button type="button" mat-stroked-button class="mat-primary" [mat-dialog-close]="true">
        {{ 'ACCEPT' | oTranslate | uppercase }} </button>
    </mat-dialog-actions>

    <!-- TEMPLATE CUSTOM FILTER-->
    <ng-template #customFilterTemplate>
      <div *ngIf="isTextType()">
        <mat-form-field fxFlex class="hinted">
          <input matInput #filterText [formControl]="fcText" placeholder="{{ 'TABLE.FILTER' | oTranslate }}">
          <mat-hint>{{ 'TABLE.FILTER_BY_COLUMN.HINT_STAR' | oTranslate }}</mat-hint>
        </mat-form-field>
      </div>
      <div *ngIf="isNumericType()" fxLayout="row" fxLayoutAlign="space-between center">
        <mat-form-field fxFlex="45">
          <input matInput [formControl]="fcFrom" placeholder="{{ 'TABLE.FILTER_BY_COLUMN.FROM' | oTranslate }}">
        </mat-form-field>
        <mat-form-field fxFlex="45">
          <input matInput [formControl]="fcTo" placeholder="{{ 'TABLE.FILTER_BY_COLUMN.TO' | oTranslate }}">
        </mat-form-field>
      </div>
      <div *ngIf="isDateType()" fxLayout="row" fxLayoutAlign="space-between center">
        <mat-form-field fxFlex="45">
          <input matInput [matDatepicker]="datepickerFrom" [max]="fcTo.value" [formControl]="fcFrom"
            placeholder="{{ 'TABLE.FILTER_BY_COLUMN.FROM' | oTranslate }}">
          <mat-datepicker-toggle matSuffix [for]="datepickerFrom">
            <mat-icon matDatepickerToggleIcon>today</mat-icon>
          </mat-datepicker-toggle>
          <mat-datepicker #datepickerFrom></mat-datepicker>
        </mat-form-field>
        <mat-form-field fxFlex="45">
          <input matInput [matDatepicker]="datepickerTo" [min]="fcFrom.value" [formControl]="fcTo"
            placeholder="{{ 'TABLE.FILTER_BY_COLUMN.TO' | oTranslate }}">
          <mat-datepicker-toggle matSuffix [for]="datepickerTo">
            <mat-icon matDatepickerToggleIcon>today</mat-icon>
          </mat-datepicker-toggle>
          <mat-datepicker #datepickerTo></mat-datepicker>
        </mat-form-field>
      </div>

    </ng-template>
  `,
  styles: [`
    .o-filter-by-column-dialog .mat-slide-toggle{padding-bottom:24px}.o-filter-by-column-dialog .content-wrapper{padding:24px 0 12px;margin:0}.o-filter-by-column-dialog .content-wrapper.content-wrapper-fixed-dimension{max-height:430px;min-height:430px;min-width:250px}.o-filter-by-column-dialog .content-wrapper .mat-form-field.hinted{margin-bottom:24px}.o-filter-by-column-dialog .content-wrapper .select-all-checkbox{padding-bottom:6px}.o-filter-by-column-dialog .content-wrapper .select-values-list{padding-top:0;overflow-y:auto;overflow-x:hidden;outline:none}.o-filter-by-column-dialog .content-wrapper .select-values-list .mat-list-item{height:30px}.o-filter-by-column-dialog .content-wrapper .select-values-list .mat-list-item .mat-list-text{white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.o-filter-by-column-dialog .content-wrapper .select-values-list .mat-list-item .mat-list-text .mat-icon{font-size:24px}.o-filter-by-column-dialog .content-wrapper .column-filter-empty-list{text-align:center}
  `],
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    '[class.o-filter-by-column-dialog]': 'true'
  }
})
export class OTableFilterByColumnDataDialogComponent implements AfterViewInit {

  column: OColumn;
  preloadValues: boolean = true;
  mode: string;
  private isCustomFilterSubject = new BehaviorSubject<boolean>(false);
  isCustomFilter: Observable<boolean> = this.isCustomFilterSubject.asObservable();

  private isDefaultFilterSubject = new BehaviorSubject<boolean>(false);
  isDefaultFilter: Observable<boolean> = this.isDefaultFilterSubject.asObservable();

  fcText = new FormControl();
  fcFrom = new FormControl();
  fcTo = new FormControl();

  protected columnData: Array<ITableFilterByColumnDataInterface> = [];
  protected tableData: Array<any> = [];
  protected _listData: Array<ITableFilterByColumnDataInterface>;

  @ViewChild('filter') filter: ElementRef;
  @ViewChild('filterValueList') filterValueList: MatSelectionList;

  constructor(
    public dialogRef: MatDialogRef<OTableFilterByColumnDataDialogComponent>,
    @Inject(MAT_DIALOG_DATA) data: any
  ) {
    if (data.column) {
      this.column = data.column;
    }
    let previousFilter: IColumnValueFilter = {
      attr: undefined,
      operator: undefined,
      values: undefined
    };
    if (data.mode) {
      this.isDefaultFilterSubject.next(data.mode === 'default');
      this.isCustomFilterSubject.next(data.mode === 'custom');
      this.mode = data.mode;
    }

    if (data.previousFilter) {
      previousFilter = data.previousFilter;
      this.isCustomFilterSubject.next([ColumnValueFilterOperator.LESS_EQUAL, ColumnValueFilterOperator.MORE_EQUAL, ColumnValueFilterOperator.BETWEEN, ColumnValueFilterOperator.EQUAL].indexOf(previousFilter.operator) !== -1);
    }
    if (data.hasOwnProperty('preloadValues')) {
      this.preloadValues = data.preloadValues;
    }
    if (data.tableData && Array.isArray(data.tableData)) {
      this.getDistinctValues(data.tableData, previousFilter);
      this.initializeCustomFilterValues(previousFilter);
      this.initializeDataList(previousFilter);
    }

    if (data.mode) {
      this.mode = data.mode;
    }
  }

  ngAfterViewInit() {
    this.initializeFilterEvent();
  }

  get listData(): Array<ITableFilterByColumnDataInterface> {
    return this._listData;
  }

  set listData(arg: Array<ITableFilterByColumnDataInterface>) {
    this._listData = arg;
  }

  initializeDataList(filter?: IColumnValueFilter): void {
    if (this.preloadValues || (filter && filter.operator === ColumnValueFilterOperator.IN)) {
      this.listData = this.columnData.slice();
    }
  }

  initializeFilterEvent() {
    if (this.filter) {
      const self = this;
      fromEvent(this.filter.nativeElement, 'keyup')
        .pipe(debounceTime(150))
        .pipe(distinctUntilChanged())
        .subscribe(() => {
          let filterValue: string = self.filter.nativeElement.value;
          filterValue = Util.normalizeString(filterValue);
          if (filterValue.indexOf('*') !== -1) {
            self.listData = self.columnData.filter(item => new RegExp('^' + Util.normalizeString(filterValue).split('*').join('.*') + '$').test(Util.normalizeString(item.value)));
          } else {
            self.listData = self.columnData.filter(item => (Util.normalizeString(item.value).indexOf(filterValue) !== -1));
          }
        });
    }
  }

  initializeCustomFilterValues(filter: IColumnValueFilter): void {
    if (filter.operator !== ColumnValueFilterOperator.IN) {
      if (ColumnValueFilterOperator.EQUAL === filter.operator) {
        if (this.isTextType()) {
          this.fcText.setValue(filter.values);
        }
      }
      if (filter.operator === ColumnValueFilterOperator.BETWEEN) {
        if (this.isDateType()) {
          this.fcFrom.setValue(new Date(filter.values[0]));
          this.fcTo.setValue(new Date(filter.values[1]));
        } else {
          this.fcFrom.setValue(filter.values[0]);
          this.fcTo.setValue(filter.values[1]);
        }
      } else {
        if (filter.operator === ColumnValueFilterOperator.MORE_EQUAL) {
          if (this.isDateType()) {
            this.fcFrom.setValue(new Date(filter.values));
          } else {
            this.fcFrom.setValue(filter.values);
          }
        }
        if (filter.operator === ColumnValueFilterOperator.LESS_EQUAL) {
          if (this.isDateType()) {
            this.fcTo.setValue(new Date(filter.values));
          } else {
            this.fcTo.setValue(filter.values);
          }
        }
      }
    }
  }

  get selectedValues(): Array<ITableFilterByColumnDataInterface> {
    return this.filterValueList ? this.filterValueList.selectedOptions.selected : [];
  }

  areAllSelected(): boolean {
    return this.selectedValues.length === this.columnData.length;
  }

  isIndeterminate(): boolean {
    return this.selectedValues.length > 0 && this.selectedValues.length !== this.columnData.length;
  }

  onSelectAllChange(event: MatCheckboxChange) {
    if (event.checked) {
      this.filterValueList.selectAll();
    } else {
      this.filterValueList.deselectAll();
    }
  }

  getDistinctValues(data: Array<any>, filter: IColumnValueFilter): void {
    let colValues: any[] = data.map(elem => elem[this.column.attr]);
    colValues.forEach((value, i) => {
      if (this.columnData.find(item => item.value === value) === undefined) {
        this.columnData.push({
          value: value,
          selected: filter.operator === ColumnValueFilterOperator.IN && (filter.values || []).indexOf(value) !== -1
        });
        this.tableData.push(data[i]);
      }
    });
  }

  getColumnValuesFilter(): IColumnValueFilter {
    let filter = {
      attr: this.column.attr,
      operator: undefined,
      values: undefined
    };

    if (!this.isCustomFilterSubject.getValue()) {
      if (this.selectedValues.length) {
        filter.operator = ColumnValueFilterOperator.IN;
        filter.values = this.selectedValues.map((item) => item.value);
      }
    } else {
      if (this.fcText.value) {
        filter.operator = ColumnValueFilterOperator.EQUAL;
        filter.values = this.getTypedValue(this.fcText);
      }
      if (this.fcFrom.value && this.fcTo.value) {
        filter.operator = ColumnValueFilterOperator.BETWEEN;
        let fromValue = this.getTypedValue(this.fcFrom);
        let toValue = this.getTypedValue(this.fcTo);
        filter.values = fromValue <= toValue ? [fromValue, toValue] : [toValue, fromValue];
      } else {
        if (this.fcFrom.value) {
          filter.operator = ColumnValueFilterOperator.MORE_EQUAL;
          filter.values = this.getTypedValue(this.fcFrom);
        }
        if (this.fcTo.value) {
          filter.operator = ColumnValueFilterOperator.LESS_EQUAL;
          filter.values = this.getTypedValue(this.fcTo);
        }
      }
    }
    return filter;
  }

  onSlideChange(e: MatSlideToggleChange): void {
    this.isCustomFilterSubject.next(e.checked);

    if (!e.checked) {
      //Selection mode
      this.initializeDataList();
      const self = this;
      setTimeout(() => {
        self.initializeFilterEvent();
      }, 0);
    }
  }

  isTextType(): boolean {
    return !this.isNumericType() && !this.isDateType();
  }

  isNumericType(): boolean {
    return ['integer', 'real', 'currency'].indexOf(this.column.type) !== -1;
  }

  isDateType(): boolean {
    return 'date' === this.column.type;
  }

  getRowValue(i: number): any {
    return this.tableData[i];
  }

  getFixedDimensionClass() {
    return this.mode === 'selection' || this.mode === 'default';
  }

  protected getTypedValue(control: FormControl): any {
    let value = control.value;
    if (this.isNumericType()) {
      value = control.value;
    }
    if (this.isDateType()) {
      value = control.value.valueOf();
    }
    return value;
  }

}
